          childGenes.value = [];
          trackBrowserGenes.value = [];
          ev.forEach((v: string, i: number) => {
            childGenes.value.push(v);
          });
          if (ev.length === 1 && geneMotif.value !== 'eRegulon') {
            ev.forEach((v: string, i: number) => {
              trackBrowserGenes.value.push(v);
            });
            if (geneMotif.value === 'motif') {
              seqlogo();
            }
          }
        });
      },
    }));
    watch(peakViewerFlag, (v: any) => {
