        else if (v === 'motif') span.innerText = 'MOTIF';
        else if (v === 'eRegulon') span.innerText = 'EREGULON';
        userMaxValue.value = '';
        userMinValue.value = '';
        userMaxMinValue.value = [userMaxValue.value, userMinValue.value];
        genes.value = [];
