    const data = ref<any[]>([]);
    const loading = ref<boolean>(true);

    async function nextPage(ev: any) {
      window.open(`${ev}`, '_blank');
    }
    function toAtlasXplore(run_data: any) {
      this.$emit('run-selected', run_data.ngs_id);
    }
    onMounted(async () => {
      // dlskmk
    });
    return {
      allRuns,
      name,
      loading,
      nextPage,
      toAtlasXplore,
      router,
    };
  },
});
